function Mailbas() {
    const [mails, setMails] = useState('');
    const [draft, setDraft] = useState('');
    const [limit, setLimit] = useState('');
    const [durums, setDurums] = useState('');
    const [tikayar, setTikayar] = useState('');
    const [emailService, setEmailService] = useState('');
    const [mintakipci, setMintakip] = useState('0');
    const [maxtakipci, setMaxtakip] = useState('0');
    const [clicked, setClicked] = useState(false);
    const [isChecked, setIsChecked] = useState(false);


    const sendEmailsSequentially = async () => {
        setClicked(true);
        setDurums("E-Posta gnderimi balatlyor....");
        const mailList = mails.split('\n');
        for (let i = 0; i < mailList.length; i++) {
            const line = mailList[i];
            const [username, email] = line.split(':'); // Satr split ederek "username" ve "Email" verilerini ayr
            if (email) {
                const modifiedDraft = draft.replace("[suphi]", username); // "[suphi]" metnini "username" ile deitir
                const url = `http://localhost:3007/api/sendEmail?to=${email}&subject=naberknk&html=${modifiedDraft}`;
                try {
                    const response = await fetch(url);
                    if (response.ok) {
                       
                        setDurums(`E-Posta Gnderildi: ${username}`);
                    } else {
                        console.error(`E-posta gnderme hatas: ${username}`);
                    }
                } catch (error) {
                    console.error(`E-posta gnderme hatas: ${username}`);
                }
            }
        }
    };


    const handleCheckboxChange = () => {
        setIsChecked(!isChecked); // Checkbox iaretliyse tersini yap
    };

    const datadancek = async () => {
        setDurums(`Datadan veriler ekiliyor, ekilen limit says : ${limit}`)
        setClicked(true);
        const username = localStorage.getItem('username');
        const url = `http://localhost:3007/api/getMailList?token=${username}&limit=${limit}&min=${mintakipci}&max=${maxtakipci}&tik=${isChecked}&emailtype=${emailService}`;
        try {
            const response = await fetch(url);
            if (response.ok) {
                const data = await response.json();
                const emailList = data
                    .filter(item => item.Email) // Bo Email alanna sahip kullanclar hari tut
                    .map(item => `${item.username}:${item.Email}`)
                    .join('\n');
                setMails(emailList);
                setDurums(`Veriler alnd.... imdi textareann iine koyulacak.`);
            } else {
                console.error(`Mail ekme hatas`);
            }
        } catch (error) {
            console.error(`Mail ekme hatas`);
        }
    }

    return (
        <>
            <GetSecure />
            <center>
                <b>Datan ekmek stediiniz Mail Saysn Yaznz: </b><input className="form-control" onChange={(e) => setLimit(e.target.value)} placeholder="MANUEL EKLCEKSENZ LTFEN BO BIRAKINIZ." /> <br />
                <b>Mail seenei : </b><select value={emailService} onChange={(e) => setEmailService(e.target.value)}>
                    <option value="farketmez">Fark etmez</option>
                    <option value="gmail">Gmail</option>
                    <option value="outlook">Outlook</option>
                    <option value="hotmail">Hotmail</option>
                    <option value="yahoo">Yahoo</option>
                    <option value="webmail">Webmail</option>
                </select><br />
                <b>User Seenei : </b>
                <b>Min takipci: </b> <input type="number" onChange={(e) => setMintakip(e.target.value)} value={mintakipci} placeholder="Min"></input><br/>
                <b>Max takipci: </b> <input type="number" onChange={(e) => setMaxtakip(e.target.value)} value={maxtakipci} placeholder="Max"></input><br/>
                <b> Tikli</b> <input
                    type="checkbox"
                    checked={isChecked}
                    onChange={handleCheckboxChange}
                />
               <br/>
                <button onClick={datadancek} className="btn btn-info">EK</button>
                <hr />
                <div className="col-12">
                    <b>Mails: </b><br />
                    <textarea
                        className="form-control"
                        rows="5"
                        value={mails}
                        onChange={(e) => setMails(e.target.value)}
                    ></textarea><br />
                    <br />
                    <hr />
                    <br />
                    <b>Taslak:(Kullanc ad gelmek istiyorsanz [suphi] yazmanz yeterli)</b><br />
                    <textarea
                        className="form-control"
                        rows="5"
                        value={draft}
                        onChange={(e) => setDraft(e.target.value)}
                    ></textarea><br />
                    {clicked && (
                        <p>{durums}</p>

                    )}
                    <button
                        className="btn btn-success"
                        onClick={sendEmailsSequentially}
                    >
                        Balat
                    </button>
                </div>
            </center>
        </>
    );
}



