function GetUserInfo() {
  const username = localStorage.getItem('username');
  const [userInfo, setUserInfo] = useState(null);
  const [loading, setLoading] = useState(true); // Veri yklenirken gstermek iin bir ykleniyor durumu ekleyin

  useEffect(() => {
    if (username) {
      fetch(`https://api.suphi.org/tokencheck.php?token=${username}`)
        .then((response) => response.json())
        .then((data) => {
          if (data.Sonuc !== 'Tamamdr') {
            localStorage.removeItem('username');
            setUserInfo(null); // Kullancy bulamadnzda userInfo'yi temizleyin
          } else {
            setUserInfo(data);
          }
          setLoading(false); // Veri ykleme tamamlandnda ykleniyor durumunu false yapn
        })
        .catch((error) => {
          console.error('API istei baarsz:', error);
          setLoading(false); // Hata durumunda da ykleniyor durumunu false yapn
        });
    } else {
      setUserInfo(null); // Kullanc ad yoksa userInfo'yi temizleyin
      setLoading(false); // Kullanc ad yoksa ykleniyor durumunu false yapn
    }
  }, [username]);

  if (loading) {
    // Veri yklenirken ykleniyor durumunu gsterin
    return <p>Ykleniyor...</p>;
  }

  if (!userInfo) {
    return <Loginsayfax />;
  }

  return userInfo;
}


