const notifAlani = document.getElementById("notif");
const notifRoot = createRoot(notifAlani);
notifRoot.render(<Notif />);


function Notif() {
    const username = localStorage.getItem('username');
    const kurl = `https://api.suphi.org/rich/notifs.php?token=${username}`;
    const [data, setData] = useState([]);
    const [dayor, setDayor] = useState(false);

    useEffect(() => {
        const theme = localStorage.getItem("theme");
        if (theme === "dark") {
            const themeLink = document.getElementById("theme-link");
            themeLink.href = "./css/vertical-layout-light/dark.css";
            setDayor(true); // Checkbox'u iaretle
        }
    }, []);

    const handleCheckboxChange = () => {
        setDayor(!dayor);
        const themeLink = document.getElementById("theme-link");
        if (dayor) {
            themeLink.href = "./css/vertical-layout-light/style.css"; // Ak tema dosyasn kullan
            localStorage.setItem("theme", "light"); // Temay sakla
        } else {
            themeLink.href = "./css/vertical-layout-light/dark.css"; // Koyu tema dosyasn kullan
            localStorage.setItem("theme", "dark"); // Temay sakla
        }
    }
    useEffect(() => {
        async function fetchData() {
            try {
                const response = await fetch(kurl);

                if (!response.ok) {
                    throw new Error('API istei baarsz oldu.');
                }

                const jsonData = await response.json();
                setData(jsonData);
            } catch (error) {
                console.error('Hata:', error);
            }
        }

        fetchData();
    }, [kurl]);

    // Statu deeri 0 olanlar say
    const unreadCount = data.filter((item) => item.Statu === "0").length;
    const okundu = async () => {
        const kurl = `https://api.suphi.org/rich/read.php?token=${username}`;
            try {
                const response = await fetch(kurl);

                if (!response.ok) {
                    throw new Error('API istei baarsz oldu.');
                }
                const jsonData = await response.json();
                if (jsonData.Sonuc == 'tamam') {
                    Swal.fire('Bildirimler', 'Hepsini okudun!', 'success')
                        .then((result) => {
                            if (result.isConfirmed) {

                                window.location.reload();
                            }
                        });
                } else {
                    Swal.fire('Bildirimer', 'Maalesef ilem baarsz.', 'error');
                }
            } catch (error) {
                console.error('Hata:', error);
            }
        
    }
    return (
        <>
         <input
                                    type="checkbox"
                                    id="dayornight"
                                    checked={dayor}
                                    onChange={handleCheckboxChange}
                                />
        <li className="nav-item dropdown">
            <a className="nav-link count-indicator dropdown-toggle" id="notificationDropdown" href="#" data-toggle="dropdown">
                <i className="icon-bell mx-0"></i>
                {unreadCount > 0 && <span className="count"></span>}
            </a>
            <div className="dropdown-menu dropdown-menu-right navbar-dropdown preview-list" aria-labelledby="notificationDropdown">
                <p className="mb-0 font-weight-normal float-left dropdown-header">Bildirimler</p>
                {data.map((item, index) => (
                    <a key={index} className="dropdown-item preview-item">
                        <div className="preview-thumbnail">
                            <div className={`${item.bgcolor}`}>
                                <i className={`${item.bgicon}`}></i>
                            </div>
                        </div>
                        <div className="preview-item-content">
                            <h6 className="preview-subject font-weight-normal">{item.icerik}</h6>
                            <p className="font-weight-light small-text mb-0 text-muted">
                                {item.tarih}
                            </p>
                        </div>
                    </a>
                ))}
                {unreadCount > 0 &&
                    <>
                    <hr /><center>
                <button onClick={okundu} className="btn btn-info">OKUNDU OLARAK ARETLE</button> 
                        </center>
            </>
                }</div>
            </li>
        </>
    );
}


